import React, { lazy, Suspense, useEffect } from 'react';

import $ from 'jquery';

// LOADING COMPONENT
import Loading from '../Loading/Loading';
// LOADING IMAGE
import LoadingImg from '../../assets/images/loading2.gif';

// REACT ROUTING PACKAGES
import { Switch, Route } from 'react-router-dom';

// import UI CSS
import './UI.css';

// IMPORT SOCKET
import socket from '../../io';

// IMPORT DASHBOARD UI
const UI = lazy( () => import ('./UI') );

// IMPORT DASHBOARD COMPONENTS
const Sidebar = lazy( () => import ('./Sidebar/Sidebar') );
const Topbar = lazy( () => import ('./Topbar/Topbar') );

// * COMPONENTS FOR STUDENT
// STUDENTS LIST
const StudentList = lazy( () => import("./Pages/Student/List/List") );
// STUDENT PORTAL STUDENT ENTRY COMPONENT
const StudentEntry = lazy( () => import("./Pages/Student/Entry/Entry") );

const Dashboard = () => {

    useEffect(
        () => {

            document.addEventListener(
                'scroll', () => {

                    if (
                        $('html').scrollTop() === 0 // IF THE DOCUMENT IS ON TOP
                    )
                    {
                        $('.Topbar').removeClass('TobarAfterScroll');
                    }else
                    {
                        $('.Topbar').addClass('TobarAfterScroll');
                    }


                }
            );

        }, []
    )

    return (
        <div className="Dashboard">
            <Sidebar />
            <div className="Content">
                <Topbar />
                <Suspense fallback={ <Loading image={ LoadingImg } width="50" height="50" txt="Loading..." unit="vh" /> }>
                    <Switch>
                      <Route exact path="/dashboard" component={ UI } />
                      <Route exact path="/students/list" component={ StudentList } />
                      <Route exact path="/students/entry=new" component={ StudentEntry } />
                    </Switch>
                </Suspense>
            </div>
        </div>
    )

}

export default Dashboard;